<template>
  <div class="shopbag">
    <div class="shopbag-bgimg"></div>
    <van-nav-bar
      title="购物袋"
      left-text="返回"
      left-arrow
      @click-left="returnLast"
      @click-right="editProduct"
      :fixed="true"
    >
      <template #right v-if="shopCount.length > 0">
        <div>{{isEdit ? '完成' : '编辑'}}</div>
      </template>
    </van-nav-bar>
    <div class="shop-pro" v-if="shopCount.length > 0">
      <div class="product" v-for="(item,index) in shopCount" :key="index">
        <van-swipe-cell :disabled="!isEdit">
          <div class="product-content clearfix">
            <div class="check fl">
              <van-checkbox v-model="item.isCheck" @change="oneCheck" checked-color="#0C34BA"></van-checkbox>
            </div>
            <div class="pro-img fl">
              <img class="auto-img" :src="item.small_img" alt />
            </div>
            <div class="pro-info fl">
              <div class="text-box">
                <div class="clearfix">
                  <div class="text-name fl">{{item.name}}</div>
                  <div class="text-rule fl">{{item.rule}}</div>
                </div>
                <div class="enname">{{item.enname}}</div>
              </div>
              <div class="price-box clearfix">
                <div class="price fl">￥{{item.price}}</div>
                <div class="fr add">
                  <van-stepper
                    v-model="item.count"
                    theme="round"
                    button-size="22"
                    disable-input
                    @change=" modifyCount(item)"
                  />
                </div>
              </div>
            </div>
          </div>
          <template #right>
            <van-button square type="danger" text="删除" @click="removeOneShop(item.sid,index)" />
          </template>
        </van-swipe-cell>
      </div>

      <div>
        <van-submit-bar
          v-show="!isEdit"
          :price="CurrenPrice"
          button-text="提交订单"
          button-color="#0C34BA"
          @submit="goPay"
        >
          <van-checkbox
            v-model="allChecked"
            icon-size="24px"
            @change="allCheck"
            checked-color="#0C34BA"
          >全选</van-checkbox>
        </van-submit-bar>

        <van-submit-bar
          v-show="isEdit"
          button-text="删除选择"
          button-color="#0C34BA"
          @submit="removeMoveShop"
        >
          <van-checkbox
            v-model="allChecked"
            icon-size="24px"
            @change="allCheck"
            checked-color="#0C34BA"
          >全选</van-checkbox>
        </van-submit-bar>
      </div>
    </div>

    <div v-else class="go-menu">
      <van-empty description="购物袋空空如也，去逛一逛！">
        <van-button round color="#0C34BA" class="bottom-button" @click="goMenu">逛一逛</van-button>
      </van-empty>
    </div>
  </div>
</template>

<script>
import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      isEdit: false,
      allChecked: false,
      shopCount: [],
      CurrenPrice: 0
    };
  },
  created() {
    this.getShopbag();
  },

  methods: {
    //回到上一级页面
    returnLast() {
      this.$router.go(-1);
    },

    //更改编辑
    editProduct() {
      this.isEdit = !this.isEdit;
    },

    //获取购物袋商品
    getShopbag() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });
      this.axios({
        method: "GET",
        url: "findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          
          this.$toast.clear();
          if (result.data.code == 5000) {
            result.data.result.map(v => {
              v.isCheck = false;
            });

            this.shopCount = result.data.result;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //全选
    allCheck() {
      this.shopCount.map(v => {
        v.isCheck = this.allChecked;
      });

      this.totalPrice();
    },

    //单选
    oneCheck() {
      this.totalPrice();

      for (let i = 0; i < this.shopCount.length; i++) {
        if (!this.shopCount[i].isCheck) {
          this.allChecked = false;
          return;
        }
      }
      this.allChecked = true;
    },

    //修改商品数量
    modifyCount(item) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sid: item.sid,
        count: item.count
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });

      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code != 6000) {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //删除商品数据
    removeShop(sids) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sids: JSON.stringify(sids)
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });

      return this.axios({
        method: "POST",
        url: "/deleteShopcart",
        data
      });
    },

    //删除单个商品
    removeOneShop(sid, index) {
      this.removeShop([sid])
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 7000) {
            this.shopCount.splice(index, 1);
          }

          this.$$toast(result.data.msg);

          this.totalPrice();
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //删除多个商品
    removeMoveShop() {
      let sids = [];
      this.shopCount.map(v => {
        if (v.isCheck) {
          sids.push(v.sid);
        }
      });

      this.removeShop(sids)
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 7000) {
            for (let i = this.shopCount.length - 1; i >= 0; i--) {
              if (this.shopCount[i].isCheck) {
                this.shopCount.splice(i, 1);
              }
            }
            this.totalPrice();
          }
          this.$toast(result.data.msg);
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //计算总额
    totalPrice() {
      this.CurrenPrice = 0;
      this.shopCount.map(v => {
        if (v.isCheck) {
          this.CurrenPrice += v.count * v.price;
        }
      });

      this.CurrenPrice *= 100;
    },

    //返回菜单
    goMenu(){
      this.$router.push({name:'Menu'})
    },

    //去到订单页面
    goPay(){
     
    //查找需要购买的商品
    let sids = [];
    this.shopCount.map( v =>{
      if (v.isCheck) {
        sids.push(v.sid);
      }
    })

    sids = sids.join('-');
    
    this.$router.push({name:'Pay',query:{sids}});
    }
  }
};
</script>

<style lang="less" scoped>
.shop-pro {
  margin-top: 46px;
  margin-bottom: 100px;
}
.product {
  margin-left: 20px;
  margin-top: 20px;

  .product-content {
    width: 100%;
    .check {
      margin-top: 30px;
      width: 24px;
      height: 24px;
    }
    .pro-img {
      width: 80px;
      height: 80px;
      margin-left: 10px;
      img {
        border-radius: 50%;
      }
    }
    .pro-info {
      width: calc(~"100% - 134px");
      margin-left: 20px;
      .text-box {
        margin-top: 23px;
        .text-name {
          font-size: 18px;
          margin-right: 10px;
          color: #0c34ba;
        }
        .text-rule {
          font-size: 14px;
          line-height: 24px;
          color: #999;
        }
        .enname {
          text-align: left;
          color: #999;
        }
      }
    }
    .price-box {
      .price {
        color: #0c34ba;
        font-size: 18px;
      }
      .add {
        margin-right: 20px;
      }
    }
  }
}
.shopbag-bgimg{
  margin-top: 46px;
  height: 80px;
  background-color: #0c34ba;
}
/deep/.van-stepper--round .van-stepper__plus {
  background-color: #0c34ba;
}
/deep/.van-stepper--round .van-stepper__minus {
  color: #1989fa;
  border-color: #1989fa;
}
/deep/.van-stepper__input {
  color: #0c34ba;
}
/deep/.van-nav-bar__text {
  color: #0c34ba;
}
/deep/.van-nav-bar .van-icon {
  color: #0c34ba;
}
/deep/.van-button--normal {
  height: 100%;
}
/deep/.van-button--danger {
  background-color: #0c34ba;
  border-color: #0c34ba;
}
/deep/.van-submit-bar {
  bottom: 50px;
}
/deep/.van-checkbox {
  margin-right: auto;
}
/deep/ .van-nav-bar__right{
  color: #0c34ba;
}
.go-menu{
  /deep/.van-button--normal{
    padding: 10px 20px;
  }
}
</style>